import axios from "axios";
import { toastMsg } from "./ToastHelper";

async function getLocation(){
    const apiKey = '8896a254c9b4438d8632ccb69b7c5a6b'; // Thay bng API Key ca bn
    async function getLocalPosition(): Promise<any> {
        return new Promise((resolve, reject) => {
            navigator.geolocation.getCurrentPosition((position => {
                const latitudeItem = position.coords.latitude;
                const longtitudeItem = position.coords.longitude;
                resolve ({latitudeItem, longtitudeItem})
            }),
            (error)=>{
                reject(error);
            }
        )
        })
    }
    
    async function formatAPILocationData(data:any) {
        // Gi API OpenCage  ly v tr a l
        const response = await axios.get('https://api.opencagedata.com/geocode/v1/json', {
            params: {
                key: apiKey,
                q: `${data.latitudeItem},${data.longtitudeItem}`, //To  ly t ngi dng (sau khi cho quyn)
                pretty: 1,
                no_annotations: 1,  // Loi b cc ch thch khng cn thit
                language: 'vi'  // Ch nh ngn ng l ting Vit
            }
        });
        if (response.status === 200) {
            const apiData: any = response.data.results[0].components;
            return apiData;
        } else {
            toastMsg.error(' c li xy ra khi nh v v tr ca bn!', '');
        }
    }
    
    if (navigator.geolocation) {
        try {
            const localPosition = await getLocalPosition();
            const apiPosition = await formatAPILocationData(localPosition);
            return apiPosition;
        } catch (error) {
            console.error('Error:', error);
        }
    } else {
        toastMsg.error('Trnh duyt khng h tr nh v v tr!', '');
    }
}
export default getLocation;